import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import { Toggle } from "carbon-components-react";
import { EventAPI } from "../../../commons/EventAPI";

const dispatchedEvents = [EventAPI.TOGGLE_CHANGED];

/*
    Example: <srs-toggle status="error" content="Please contact system administrator." prefix="error"/>
*/
const SrsToggle = function (props, ref) {
    const [inputValue, setInputValue] = useState(props.value || false);
    useEffect(() => {
        setInputValue(props.value || false);
    }, [props]);

    const handleChange = (val) => {
        props.dispatchEvent(EventAPI.TOGGLE_CHANGED, { payload: { value: val, id: props.elementid } });
        setInputValue(val);
        props.setValue(val);
        if(props.onChange) {
            props.onChange(val);
        }
    };

    const handleClick = (evt, payload) => {
        console.log(evt, payload);
    };

    return (
        <div className="cds--form-item">
            <Toggle id={props.elementid} toggled={inputValue} labelA={props.label} labelB={props.label} onToggle={handleChange}/>
        </div>
    );
}

const styles = `
    
`
const props = [{
    name: "label",
    defaultValue: "Toggle Label",
    type: "string"
}, {
    name: "value",
    defaultValue: false,
    type: "boolean"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsToggle), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };